.bullet{

  &__list{

    &_item{
      position: relative;
      padding-left: 20px;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      color: $darkshade75;

      &::before{
        content:'';
        width: 10px;
        height: 10px;
        background-color: #000;
        position: absolute;
        top: 7px;
        left: 0;
        border-radius: 100%;
        background: rgba(31, 32, 65, 0.25);
      }
    }
  }
}