.review{

  &__header{
    display: flex;
    margin-bottom: 10px;
  }

  &__name{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: rgba(31, 32, 65, 0.75);

    &-wrapper{
      display: flex;
      flex-direction: column;
    }
  }

  &__date{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: rgba(31, 32, 65, 0.75);
  }

  &__avatar{
    width: 48px;
    height: 48px;
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 32px;
    margin-right: 10px;

    &_image{
      width: 100%;
      height: 100%;
      border-radius: 100%;
      filter: drop-shadow(0px 10px 20px rgba(31, 32, 65, 0.1));
    }
  }

  &__content{
    display: flex;
  }

  &__text{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $darkshade75;
    width: 653px;
  }
}